import { GET_CATEGORIES, SET_ITEMS_PER_PAGE, SET_BRAND, SET_FILTERED_PRICE, MANAGE_CART, GET_PRODUCTS, MANAGE_WISHLIST, MANAGE_COMPARELIST, MANAGE_ROUTES, SET_FILTERED_ITEMS } from "../constants";

const initialCategories = {
  categories: [],
  selectedCategories: [],
  products: [],
  wishList: [],
  compareList: [],
  cartList: [],
  catalogRoutes: [],
  filteredItems: [],
  filteredPrice: [],
  itemsPerPage: 15,
  brand: ""
};

type initialCategoriesType = typeof initialCategories;
type actionType = {
  type: any;
  categories: any;
  selectedCategories: any;
  products: any;
  cartList: any;
  wishList: any;
  compareList: any;
  catalogRoutes: any;
  filteredItems: any;
  filteredPrice: any;
  itemsPerPage: any;
  brand: any;
};

export default (state: initialCategoriesType = initialCategories, action: actionType) => {
  switch (action.type) {
    case GET_CATEGORIES:
      return {
        ...state,
        categories: action.categories,
        selectedCategories: action.selectedCategories,
      };
    case GET_PRODUCTS:
      return {
        ...state,
        products: action.products,
      };
    case MANAGE_CART:
      return {
        ...state,
        cartList: action.cartList,
      };
    case MANAGE_WISHLIST:      
        return {
          ...state,
          wishList: action.wishList,
        };
    case MANAGE_COMPARELIST:      
        return {
          ...state,
          wishList: action.compareList,
        };
    case MANAGE_ROUTES:      
    return {
      ...state,
      catalogRoutes: action.catalogRoutes,
    };
    case SET_FILTERED_ITEMS:      
    return {
      ...state,
      filteredItems: action.filteredItems,
    };
    case SET_FILTERED_PRICE:      
    return {
      ...state,
      filteredPrice: action.filteredPrice,
    };
    case SET_ITEMS_PER_PAGE:      
    return {
      ...state,
      itemsPerPage: action.itemsPerPage,
    };
    case SET_BRAND:      
    return {
      ...state,
      brand: action.brand,
    };
    default:
      return state;
  }
};

