import Home from "../containers/Home";
import Wishlist from "../containers/Wishlist";
import Catalog from "../containers/Catalog";
import Cart from "../containers/Cart";
import Product from "../containers/Product";

export default [
  {
    url: "/ts-marketplace/",
    component: Home,
  },
  {
    url: "/ts-marketplace/c/:category",
    component: Catalog,
  },
  {
    url: "/ts-marketplace/wishlist",
    component: Wishlist,
  },
  {
    url: "/ts-marketplace/cart",
    component: Cart,
  },
  {
    url: "/ts-marketplace/p/:id",
    component: Product,
  },
];
