import "./index.scss";

import React from "react";
import { connect } from "react-redux";
import * as actions from "../../redux/actions";
import Banner from "../../components/Catalog/Banner";
import Container from "../../components/Catalog/Container";
import Filter from "../../components/Catalog/Filter";

const Catalog = (props: any) => {
  const { products, catalogRoutes, categories } = props;

  return (
    <div className={`catalog`}>
      {products.length > 0 && (
        <>
          <Filter products={products} catalogRoutes={catalogRoutes} />
          <div className="catalog__right">
            <Banner categories={categories} />
            <Container />
          </div>
        </>
      )}
    </div>
  );
};

export default connect((state) => state, actions)(Catalog);
