import "./index.scss";

import React from "react";

type itemType = { title: string; children: React.ReactNode };

const Item = (props: itemType) => {
  return (
    <section className={`categoryFilterItem`}>
      <h3 className={`categoryFilterItem__title`}>{props.title}</h3>
      {props.children}
    </section>
  );
};

export default Item;
