import "./index.scss";

import React, { useState } from "react";
import uid from "uid";
import { Tabs } from "antd";
import Reviews from "./Reviews";
import Review from "./Review";
const { TabPane } = Tabs;

const initialReviews = [
  {
    id: 0,
    name: "Oleg",
    review: "Lorem ipsum dolor sit amet, consectetur adipiscing elit",
    rate: 5,
  },
  {
    id: 1,
    name: "Oleg",
    review: "Lorem ipsum dolor sit amet, consectetur adipiscing elit",
    rate: 3,
  },
  {
    id: 2,
    name: "Oleg",
    review: "Lorem ipsum dolor sit amet, consectetur adipiscing elit",
    rate: 5,
  },
];

const ProductTabs = (props: any) => {
  const { product } = props;
  const [finalReviews, setNewReviews] = useState(initialReviews);

  const AddReview = (review: any) => {
    const newReviewsList = finalReviews.filter((item, index) => index !== 2);
    const newReviews = [review, ...newReviewsList];
    setNewReviews(newReviews);
  };

  const renderedDescription = () => {
    return product.description.map((item: string) => (
      <p key={uid()} className={`tabs-text`}>
        {item}
      </p>
    ));
  };
  const renderedFeatures = (features: { [x: string]: React.ReactNode }) => {
    return features
      ? Object.keys(features).map((key) => (
          <tr key={uid()} className={`tabs__tr`}>
            <td className={`tabs__td`}>{key}</td>
            <td className={`tabs__td`}>{features[key]}</td>
          </tr>
        ))
      : null;
  };

  return (
    <div className={`product-tabs`}>
      <Tabs defaultActiveKey="1">
        <TabPane tab="Description" key="1">
          {product && renderedDescription()}
        </TabPane>
        <TabPane tab="Main features" key="2">
          <table className={`tabs__table`}>
            <tbody>{product && renderedFeatures(product.features)}</tbody>
          </table>
        </TabPane>
        <TabPane tab="Reviews" key="3">
          <Review AddReview={AddReview} />
          <Reviews finalReviews={finalReviews} />
        </TabPane>
      </Tabs>
    </div>
  );
};

export default ProductTabs;
