import "./index.scss";

import React, { useState } from "react";
import { connect } from "react-redux";
import * as actions from "../../../redux/actions";
import Filter from "./Filter";
import { Pagination } from "antd";
import Items from "../Items";

const Container = (props: any) => {
  const {
    products,
    catalogRoutes,
    filteredPrice,
    itemsPerPage,
    brand,
    setBrand,
  } = props;
  const [currentPage, setCurrentPage] = useState(1);

  const productsByCategory =
    catalogRoutes.length > 0
      ? products.filter((item: { category: string }) =>
          catalogRoutes.some((el: string) => el === item.category)
        )
      : products;

  const productsByBrand =
    brand !== ""
      ? productsByCategory.filter((item: { features: { company: string } }) => {
          return item.features.company === brand;
        })
      : productsByCategory;

  const productsByPrice =
    filteredPrice.length > 0
      ? productsByBrand.filter(
          (item: { price: number }) =>
            item.price >= filteredPrice[0] && item.price <= filteredPrice[1]
        )
      : productsByBrand;

  const min = currentPage > 1 ? (currentPage - 1) * itemsPerPage : 0;
  const max = currentPage * itemsPerPage;

  const productsPerPage = productsByPrice.filter(
    (item: any, index: number) => index >= min && index <= max - 1
  );

  return (
    <section className={`category-container`}>
      {products.length > 0 && (
        <>
          <Filter />
          <Items products={productsPerPage} setBrand={setBrand} />
          <div className={`pagination`}>
            <Pagination
              current={currentPage}
              total={productsByPrice.length}
              pageSize={itemsPerPage}
              onChange={(event) => setCurrentPage(event)}
            />
          </div>
        </>
      )}
    </section>
  );
};

export default connect((state) => state, actions)(Container);
