import "./index.scss";
import "./icons.scss";

import React, { Fragment } from "react";
import { connect } from "react-redux";
import uid from "uid";
import {
  AiOutlineFacebook,
  AiOutlineLinkedin,
  AiOutlineShareAlt,
  AiOutlineHeart,
} from "react-icons/ai";
import { TiSocialInstagram } from "react-icons/ti";
import { RiTwitterLine, RiPinterestLine } from "react-icons/ri";
import { Tooltip } from "antd";
import * as actions from "../../../../../redux/actions";

const Bottom = (props: any) => {
  const { id, manageWishlist, wishList } = props;

  const makeWishList = () => {
    const finalWishList = Object.values(wishList).some((item) => item === id)
      ? wishList.filter((item: any) => item !== id)
      : [...wishList, id];
    manageWishlist(finalWishList);
  };

  const items = [
    {
      title: "Add to Wishlist",
      icon: <AiOutlineHeart />,
      func: () => makeWishList(),
    },
    {
      title: "Share",
      icon: <AiOutlineShareAlt />,
      social: [
        {
          title: "facebook",
          url: "https://www.facebook.com/",
          icon: <AiOutlineFacebook />,
        },
        {
          title: "instagram",
          url: "https://www.instagram.com/",
          icon: <TiSocialInstagram />,
        },
        {
          title: "twitter",
          url: "https://twitter.com/",
          icon: <RiTwitterLine />,
        },
        {
          title: "linkedin",
          url: "https://www.linkedin.com/",
          icon: <AiOutlineLinkedin />,
        },
        {
          title: "pinterest",
          url: "https://www.pinterest.com/",
          icon: <RiPinterestLine />,
        },
      ],
    },
  ];

  const createSocial = (icons: any[] | undefined) => {
    return (
      <ul className={`icons`}>
        {icons &&
          icons.map((item) => (
            <li key={uid()} className={`icons__item`}>
              <a
                href={item.url}
                target="_blank"
                rel="noopener noreferrer"
                className={`icons__link`}
              >
                {item.icon}
              </a>
            </li>
          ))}
      </ul>
    );
  };

  const newItems = items.map((item) => (
    <Fragment key={uid()}>
      {item.title === "Share" ? (
        <Tooltip  placement="bottomRight" title={createSocial(item.social)}>
          <li className={`product-main__bottom-item`}>
            <span className={`product-main__bottom-icon`}>{item.icon}</span>
            <span className={`product-main__bottom-title`}>{item.title}</span>
          </li>
        </Tooltip>
      ) : (
        <li
          className={`product-main__bottom-item`}
          onClick={item.func}
        >
          <span className={`product-main__bottom-icon`}>{item.icon}</span>
          <span className={`product-main__bottom-title`}>{item.title}</span>
        </li>
      )}
    </Fragment>
  ));

  return <ul className={`product-main__bottom`}>{newItems}</ul>;
}

export default connect((state) => state, actions)(Bottom);
