import "./index.scss";

import React from "react";
import uid from "uid";
import { Rate } from "antd";

const Reviews = (props: { finalReviews: any }) => {
  const { finalReviews } = props;

  const filteredByRate: any = finalReviews.filter(
    (item: { hasOwnProperty: (arg0: string) => any }) =>
      item.hasOwnProperty("rate")
  );
  const rate = filteredByRate.reduce(
    (acc: number, item: { rate: number }) => acc + item.rate,
    0
  );
  const filteredByRateAmount = filteredByRate.length;
  const mainRate = Math.round(rate / filteredByRateAmount);

  const newReviews = (items: any[]) => {
    return items.map((item) => (
      <article key={uid()} className={`reviews__item`}>
        <h4 className={`reviews__name`}>{item.name}</h4>
        <Rate disabled defaultValue={item.rate} />
        <p className={`reviews__text`}>{item.review}</p>
      </article>
    ));
  };

  return (
    <div className={`reviews`}>
      <h3 className={`reviews__title`}>
        Reviews <Rate disabled value={mainRate} />
      </h3>
      <div className={`reviews__container`}>{newReviews(finalReviews)}</div>
    </div>
  );
}

export default Reviews;
