import React from "react";
import { Link } from "react-router-dom";
import { IoIosArrowRoundForward } from "react-icons/io";

const Item = (props: any) => {
  const { title, images, category, price, id } = props.item;

  return (
    <article className={`scroll-slider__item-wrapper`}>
      <div className={`scroll-slider__item`}>
        <div className="scroll-slider__top">
          <h3 className="scroll-slider__item-title">{category}</h3>
          <Link to={`/ts-marketplace/p/${id}`} className={`scroll-slider__link`}>
            View all details <IoIosArrowRoundForward />
          </Link>
        </div>
        <div className="scroll-slider__bottom">
          <Link to={`/ts-marketplace/p/${id}`} className={`scroll-slider__link`}>
            {title}
          </Link>
          <p className={`scroll-slider__pricing`}>
            <strong className="scroll-slider__price">${price}</strong>
          </p>
        </div>
        <img src={images[0]} alt={title} className={`scroll-slider__image`} />
      </div>
    </article>
  );
};

export default Item;
