import "./index.scss";

import React from "react";
import uid from "uid";
import { Rate, Form, Input, Button } from "antd";
const { TextArea } = Input;

const layout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 16 },
};

const validateMessages = {
  required: "${label} is required!",
  types: {
    email: "${label} is not validate email!",
    number: "${label} is not a validate number!",
  },
  number: {
    range: "${label} must be between ${min} and ${max}",
  },
};

const Review = (props: any) => {
  const { AddReview } = props;
  const [form] = Form.useForm();

  const onFinish = (values: any) => {
    const obj = { ...values.user, id: uid() };
    AddReview(obj);
    form.resetFields();
  };

  return (
    <Form
      {...layout}
      name="nest-messages"
      form={form}
      onFinish={onFinish}
      validateMessages={validateMessages}
    >
      <Form.Item
        name={["user", "rate"]}
        label="Rate"
        rules={[{ required: true }]}
      >
        <Rate />
      </Form.Item>
      <Form.Item
        name={["user", "name"]}
        label="Name"
        rules={[{ required: true }]}
      >
        <Input />
      </Form.Item>
      <Form.Item
        name={["user", "email"]}
        label="Email"
        rules={[{ type: "email" }]}
      >
        <Input />
      </Form.Item>
      <Form.Item
        name={["user", "review"]}
        label="Review"
        rules={[{ required: true }]}
      >
        <TextArea />
      </Form.Item>
      <Form.Item wrapperCol={{ ...layout.wrapperCol, offset: 8 }}>
        <Button type="primary" htmlType="submit">
          Submit
        </Button>
      </Form.Item>
    </Form>
  );
};

export default Review;
