import "./index.scss";

import React, { useState, Fragment } from "react";
import { connect } from "react-redux";
import * as actions from "../../../../redux/actions";
import { Link } from "react-router-dom";
import { AiOutlineAppstore } from "react-icons/ai";
import { BsPlus } from "react-icons/bs";
import uid from "uid";

const Categories = (props: any) => {
  const { categories, manageRoutes, setBrand } = props;

  const [openedItem, setOpenedCategory] = useState(100);

  const toggleCategory = (id: number) => {
    if (id === openedItem) {
      setOpenedCategory(100);
    } else {
      setOpenedCategory(id);
    }
  };

  const setNewRoutes = (routes: string[]) => {
    manageRoutes(routes);
    setBrand("");
  };

  const filterItems = categories;

  const setItems = (items: any[]) => {
    return items.map(
      (item: {
        mainLink: string;
        routes: string[];
        title: string;
        id: number;
      }) => (
        <Fragment key={uid()}>
          <li className={`filter-categories__item`}>
            <div className={`filter-categories__left`}>
              <Link
                to={`/ts-marketplace/c/${item.mainLink}`}
                onClick={() => setNewRoutes(item.routes)}
                className={`filter-categories__link`}
              >
                <AiOutlineAppstore />
                <span className={`filter-categories__title`}>{item.title}</span>
              </Link>
            </div>
            {item.routes && (
              <BsPlus
                className={`filter-categories__plus`}
                onClick={() => toggleCategory(item.id)}
              />
            )}
          </li>
          {openedItem === item.id &&
            item.routes &&
            item.routes.map((route: string) => (
              <li
                key={uid()}
                className={`filter-categories__item filter-categories__item--sub`}
              >
                <div className={`filter-categories__item`}>
                  <Link
                    to={`/ts-marketplace/c/${route}`}
                    onClick={() => setNewRoutes([route])}
                    className={`filter-categories__link`}
                  >
                    <AiOutlineAppstore />
                    <span className={`filter-categories__title`}>{route}</span>
                  </Link>
                </div>
              </li>
            ))}
        </Fragment>
      )
    );
  };

  return <ul className={`filter-categories`}>{setItems(filterItems)}</ul>;
};

export default connect((state) => state, actions)(Categories);
