import "./index.scss";

import React from "react";
import { Link } from "react-router-dom";
import uid from "uid";

type itemsType = { products: any[]; setBrand: (event: string) => void };

const Items = (props: itemsType) => {
  const { products, setBrand } = props;

  const newItems = products.map((item: any) => (
    <article key={uid()} className={`catalog__item`}>
      <div className="catalog__item-top">
        <Link to={`/ts-marketplace/p/${item.id}`} onClick={() => setBrand("")}>
          <img
            src={item.images[0]}
            alt={item.title}
            className={`catalog__image`}
          />
        </Link>
      </div>
      <div className="catalog__item-bottom">
        <Link to={`/ts-marketplace/p/${item.id}`}>
          <h3 className="catalog__title">{item.title}</h3>
        </Link>
        <span className="catalog__price">${item.price}</span>
      </div>
    </article>
  ));

  return <section className={`catalog__items`}>{newItems}</section>;
};

export default Items;
