import "./index.scss";

import React from "react";
import "antd/dist/antd.css";
import Slider from "./Slider";
import Right from "./Right";

const Main = (props: { product: any }) => {
  const { product } = props;

  return (
    <div className={`product-main`}>
      <Slider product={product} />
      <Right product={product} />
    </div>
  );
};

export default Main;
