import "./index.scss";

import React, { Fragment } from "react";
import { connect } from "react-redux";
import * as actions from "../../../../redux/actions";
import uid from "uid";
import { AiOutlineMinusSquare } from "react-icons/ai";

const Brands = (props: any) => {
  const { products, catalogRoutes, setBrand } = props;

  const createFilteredItems = () => {
    const newProducts = catalogRoutes
      ? products.filter((item: { category: string }) =>
          catalogRoutes.some((el: string) => el === item.category)
        )
      : products;

    let filterItems: any[] = [];

    newProducts.forEach((item: { features: { company: string } }) => {
      if (!filterItems.some((el) => el.title === item.features.company)) {
        const amount = newProducts.filter(
          (el: { features: { company: string } }) =>
            el.features.company === item.features.company
        );

        const obj = {
          id: uid(),
          title: item.features.company,
          amount: amount.length,
        };

        filterItems = [...filterItems, obj];
      }
    });
    return filterItems;
  };

  const setItems = (items: any[]) => {
    return items.map((item: { title: string; amount: number }) => (
      <Fragment key={uid()}>
        <li className={`filter-brands__item`}>
          <div className={`filter-brands__item`}>
            <AiOutlineMinusSquare className={`filter-brands__minus`} />
            <p className={`filter-brands__title`}>
              <span
                className={`filter-brands__title-first`}
                onClick={() => setBrand(item.title)}
              >
                {item.title}
              </span>
              <span className={`filter-brands__amount`}>({item.amount})</span>
            </p>
          </div>
        </li>
      </Fragment>
    ));
  };

  return products.length > 0 && catalogRoutes.length === 1 ? (
    <ul className={`filter-brands`}>{setItems(createFilteredItems())}</ul>
  ) : null;
}

export default connect((state) => state, actions)(Brands);
